.dish_type_card{
    
    min-width: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
    padding: 15px;
    border-radius: 20px;
    cursor: pointer;

    fill: var(--primary-color);
    color: var(--primary-color);
    background-color: white;
    -webkit-filter: drop-shadow(6px -6px 30px rgba(0, 0, 0, 0.1)) drop-shadow(6px 6px 30px rgba(0, 0, 0, 0.1));
            filter: drop-shadow(6px -6px 30px rgba(0, 0, 0, 0.1)) drop-shadow(6px 6px 30px rgba(0, 0, 0, 0.1));
}

.dish_type_card--active{
    min-width: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
    padding: 15px;
    border-radius: 20px;
    cursor: pointer;

    /* active styles */
    fill: var(--primary_color--light);
    color: var(--primary_color--light);
    background-color: var(--accent_color);
    -webkit-filter: none;
            filter: none;
}

.dish_type_card__image{
    width: 45px;
    height: 45px;
    padding: 5px;
    border-radius: 10px;
    background-color: white;

}

.dish_type_card__image > img{
    width: 100%;
    height: auto;
}


.dish_type_card__name{

    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    margin: 23px 0px;
    color: inherit;
}

.dish_type_card__button,
.dish_type_card__button > svg,
.dish_type_card__button > svg > path{
    fill: inherit;
}

