.dish_menu_card{
    width: calc(100% - 20px);
    padding: 10px;
    box-shadow: -6px -6px 30px rgba(0, 0, 0, 0.15), 6px 6px 30px rgba(0, 0, 0, 0.15);
    border-radius: 15px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    justify-self: center;

    /* transition: width 1s, opacity 0.5s;
    overflow: hidden; */

     
}




.dish_menu_card__image{
    width: calc(100% - 40px);
    min-height: 120px;
    padding: 20px;
    border-radius: 15px;
    background-color: var(--accent_color);
    margin-bottom: 18px;

    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;

    /* animation on image loading */
    background: linear-gradient(-70deg, rgba(200, 22, 28, 0.876) 45%, #df1920 50%, rgba(200,22,29,1) 55%);
    background: linear-gradient(-70deg, rgba(200, 22, 28) 45%, rgba(200, 22, 28, 0.75) 50%, rgba(200,22,29,1) 55%);
	background-size: 400% 400%;
	-webkit-animation: gradient 2.5s linear infinite;
	        animation: gradient 2.5s linear infinite;
        
}


@-webkit-keyframes gradient {
	0% {
        background-position: 100% 50%;
	}
	100% {
        background-position: 0% 50%;
	}
}


@keyframes gradient {
	0% {
        background-position: 100% 50%;
	}
	100% {
        background-position: 0% 50%;
	}
}

.dish_menu_card__image > div{
    position: absolute;
    top: 5px;
    right: 5px
}

.dish_menu_card__image > img{
