.btn{
    cursor: pointer;
}

.btn__square{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    cursor: pointer;
    background-color: var(--primary_color--light);
}

.btn__square--active{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    cursor: pointer;

    /* active styles */
    color: var(--primary_color--light);
    background-color: var(--accent_color);
}

/* MODIFICATIONS */

.btn__square--shadow{
    box-shadow: -4px -2px 15px rgba(0, 0, 0, 0.15), 4px 4px 15px rgba(0, 0, 0, 0.15);
}

.btn__square--small{
    width: 40px;
    height: 40px;
}

.btn__square--medium{
    width: 50px;
    height: 50px;

    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
}






.btn__circle{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    cursor: pointer;
    background-color: var(--primary_color--light);
}

/* MODIFICATIONS */

.btn__circle--shadow{
    box-shadow: -4px -2px 15px rgba(0, 0, 0, 0.15), 4px 4px 15px rgba(0, 0, 0, 0.15);
}

.btn__circle--small{
    width: 40px;
    height: 40px;

    font-weight: normal;
    font-size: 20px;
    line-height: 30px;  
}

.btn__circle--medium{
    width: 50px;
    height: 50px;

    font-weight: normal;
    font-size: 20px;
    line-height: 30px;  
}

.btn__circle--light_accent{
    color: var(--primary_color);
    background-color: var(--accent_color--light);
}







.btn__rect{
    width: calc(100% - 38px);
    padding: 19px;
